.policy-mobile-seize {
  width: 100%;
  height: 100px;
}

.policy-mobile {
  overflow-y: scroll;
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.policy-mobile-card-box:nth-of-type(1) {
  margin-top: 40px;
}

.policy-mobile-card {
  width: 710px;
}

.policy-mobile-card > p {
  font-size: 30px;
  line-height: 50px;
  color: #333;
}

.policy-mobile-card > p:nth-of-type(2) > span {
  color: #00a5e0;
}

.policy-mobile-card-divider {
  width: 710px;
}
